@import url('https://fonts.cdnfonts.com/css/whitney-2');
@import url('https://fonts.cdnfonts.com/css/droid-sans-mono-2');

html {
    overflow-x: hidden;
}

body {
    overflow-x: hidden;
    margin: 0px;
    color: #dde;
    background-color: #223;
    font-family: 'Whitney', Arial, Helvetica, sans-serif, 'Material Symbols Outlined';
    display: flex;
    flex-direction: column;
    position: absolute;
    inset: 0px;
}

.material-symbols-outlined {
    user-select: none;
}

hr {
    background-color: #445;
    margin: 0px;
    margin-bottom: 20px;
    border: none;
    height: 1px;
    width: 100%;
}

main {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    padding: 20px;
    flex-grow: 1;

    .container {
        max-width: 1250px;
        flex-grow: 1;
        width: 100%;
    }
}

a.proper {
    color: #88f;

    &:visited, &:active {
        color: #f88;
    }
}

button {
    padding: 12px;
    margin-left: 7px;
    border-radius: 10px;
    background-color: #112;
    border: #334 2px solid;
    transition: background-color 70ms ease-out;
}

body:not(.touch) {
    button:hover {
        background-color: #eef !important;
        border: transparent 2px solid;
        color: #223;
    }

    button:hover {
        background-color: #dde !important;
        color: #223;
    }
}

body.touch {
    button:active {
        background-color: #eef !important;
        border: transparent 2px solid;
        color: #223;
    }

    button:active {
        background-color: #dde !important;
        color: #223;
    }
}

.jump {
    position: fixed;
    bottom: 20px;
    opacity: 1;
    right: 20px;
    z-index: 2;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: bottom 100ms ease-out, opacity 100ms ease-out, background-color 70ms ease-out;

    p {
        margin: 0px;
        margin-left: 7px;
    }

    &.hidden {
        bottom: 0px;
        opacity: 0;
        pointer-events: none;
    }
}